import {supabase} from "../ConfigSupabase";

export async function fetchBuses(choferes){
   
   var search = [];
   for(var chofer of choferes){
      if(chofer.id !== undefined)
         search.push('id.eq.'+chofer.id);
   }

   let { data: Location, error } = await supabase
      .from('Location')
      .select('*')
      .or(search.toString())

   var coords = [];
   var names = [];
   
   if(error !== null) 
      return {coords: coords, names: names};

   for(var location of Location) {    
      var {Latitude, Longitude} = location;
      var { Nombre } = location;
      coords.push([Latitude, Longitude]);
      names.push(Nombre);
   }
   return {coords: coords, names: names};
}